export const Icon = React.forwardRef((props, ref) => (
  <Box
    ref={ref}
    css={`
      ${props =>
        css({
          '&:hover': {
            color: props.hovercolor,
          },
        })}
    `}
    {...props}
  >
    <svg
      viewBox={viewBoxes[props.variant]}
      fill="currentColor"
      width="100%"
      height="100%"
    >
      <title>
        {!props.showTitle
          ? ''
          : props.variant === 'logo'
          ? 'Colin Spence Design'
          : props.variant}
      </title>
      {eval(props.variant)}
    </svg>
  </Box>
))

Icon.propTypes = {
  variant: PropTypes.oneOf(Object.keys(viewBoxes)).isRequired,
}
